<template>
	<div id="NoteContainer" :class="{focused:isFocused}">
		<NoteInstance class="instance" :noteid="0"/>
	</div>
</template>

<script>
import {mapState, mapGetters} from 'vuex';
import NoteInstance from './NoteInstance.vue';
import helperfns from '../helperfns.js';
import {Note} from '../store.js';

const copy_options = {COPY: 1, CUT:2, DEEPCOPY:3};

export default {

	name: 'NoteContainer',
	
	components: {
		NoteInstance
	},

	data(){
		
		return {	
			_selectedInstances : [null],//noteInstance functions act on these when user invokes
			_focusedInstance: null,//instance with cursor
			noteContainer: this,
			copiedInstances: [],
			copy_state: null,
			active_modes : {
				"b" : false,
				"i" : false, 
				"u" : false	
			}

		}
	},

	methods: {
		 onRightClick (e) {
            alert(`You clicked ${e}!`);
        },

		select(instance,appendMode=false){
			if (!appendMode){ this.selectedInstances = []}
			this.selectedInstances = [...this.selectedInstances, instance];
		},

		focus(instance,appendMode=false){
			this.focusedInstance = instance;
			this.select(instance,appendMode);
		},

		clearSelectedInstances(){this.selectedInstances = []},

		getNewNote(){ 
			//Get a new Note
			var nextID = this.$store.getters.nextID;
			this.$store.commit('registerNewNote',nextID);
			var newNote = this.$store.getters.getNoteByID(nextID);
			return newNote;
		},

		swapInstances(i1,i2){
			//For now, can only swap siblings
			if(i1.$parent!=i2.$parent){return}

			//Swap the children of the instances
			var parent1 = i1.$parent.note; 
			var indx1 = parent1.children.indexOf(i1.note.id);
			var parent2 = i2.$parent.note; 
			var indx2 = parent2.children.indexOf(i2.note.id);
			parent1.children[indx1] = i2.note.id;
			parent2.children[indx2] = i1.note.id;

			//Swap the expanded state 
			//[i1.button_text,i2.button_text]=[i2.button_text,i1.button_text];
			
			i1.$parent.$forceUpdate();
			if(i1.$parent != i2.$parent)
			i2.$forceUpdate();
		},

		addSelectedInstancesToClipboard(){
			this.copiedInstances = this.selectedInstances.filter(x=>true);
		},

		canCopy(parent,child){
			//Can't copy in parent already has that note
			if (parent.getChildInstances().filter(inst => inst.note == child.note).length > 0){return false}

			//Can't add copy something into itself. Nonsensical.
			if(parent.note == child.note){return false}

			return true; 
		},

		//Parent and child are INSTANCES 
		copyOne(parent,child,copy_option){
			if(copy_option == copy_options.CUT){
				child.removeFromParent()
			}
			
			if ([copy_options.CUT,copy_options.COPY].includes(copy_option)){
				parent.addNote(child.note);
				parent.expand();
			}

			else if(copy_option == copy_options.DEEPCOPY){
				this.deepCopy(parent,child);
				parent.expand();

			}
		},

		//This is only sort-of a deep copy. It makes a new note which copies the text by value, and the child arrays by values. The children themselves are not cloned.
		//Parent and child are INSTANCES
		deepCopy(parent,child){
				var newNoteID = this.$store.getters.nextID;
				this.$store.commit('registerNewNote',newNoteID);
				var newNote = this.$store.getters.getNoteByID(newNoteID);
				
				newNote.markup = child.note.markup;
				newNote.children = child.note.children.filter(x=>true);
				newNote.parents = child.note.parents.filter(x=>true);

				this.$store.commit("addChildToParent",{child:newNote,parent});
		},

		hardDeleteAllSelectedInstances(){
			this.selectedInstances.forEach(instance => instance.hardDelete());
		}

	},
 
	computed: {
	
		selectedInstances : {
			set(instances){
				this.$data._selectedInstances = instances;	
			},
			get(){
				return this.$data._selectedInstances;
			}
		},

		focusedInstance: {
			set(instance){
				
				this.$data._focusedInstance = instance;
			},

			get(){
				return this.$data._focusedInstance;
			}
		},

		showMarkup : {
			set(value){this.$store.state.settings.showMarkup = value},
			get(){return this.$store.state.settings.showMarkup}
		},

		isFocused(){
			return this.focusedInstance == this.$children[0];
		},

		children(){return this.$children},

		rootInstance(){return this.$children[0]}

		

	},

	created(){
		document.addEventListener('keydown', e => {

			switch(e.key){
				case 'Enter':
					if (this.focusedInstance.isRoot){break}

					if(!this.focusedInstance.isEditable){
						this.focusedInstance.isEditable=true;
						e.preventDefault();
					}
					
					break;

				case "Alt":
					e.preventDefault();
					if(this.focusedInstance.isEditable){
						this.focusedInstance.isEditable=false;
					}
					break;

				case 'Insert':
					e.preventDefault();
					if(this.focusedInstance.isEditable){break;}
					var newNote = this.getNewNote();
					this.selectedInstances.forEach(function(instance){
						instance.addNote(newNote);
						instance.expand();
					});
					//Focus newNote under focused instance
					this.$nextTick(function(){
						
						var children = this.focusedInstance.getChildInstances();
						var newInst = children.filter(x=>x.note==newNote)[0];
						if (!e.altKey){newInst.focus()}
						}.bind(this));
					
					break;

				case "ArrowUp":
				case "ArrowDown":
					if(this.focusedInstance.isEditable){break;}
					if (e.shiftKey && e.ctrlKey){break}
					
					e.preventDefault();

					var fi = this.focusedInstance;
					var instanceToActOn; 

					if (e.key=="ArrowUp")	{instanceToActOn = fi.getPreviousVisibleInstance();}
					if (e.key=="ArrowDown")	{instanceToActOn = fi.getNextVisibleInstance();}
					if (instanceToActOn == null){return;}
					
					if (!e.shiftKey){
						this.focus(instanceToActOn,e.ctrlKey)
					}
					if (e.shiftKey){
						this.swapInstances(fi,instanceToActOn);
						if(!document.title.endsWith('*')){document.title+='*'}		
					}
					break;

				case "ArrowLeft":
					if (this.focusedInstance.isEditable){break;}
					
					e.preventDefault();
					if (this.focusedInstance.isRoot) {break;}
					this.focusedInstance.$parent.focus();
					break; 

				case "ArrowRight":
					if (this.focusedInstance.isEditable){break;}
					
					e.preventDefault();
					this.focusedInstance.expand();
					this.$nextTick(()=>{
						var children = this.focusedInstance.getChildInstances();
						if (children.length>0){children[0].focus();}
					})
					break;

				case "Tab":
					e.preventDefault();
					if(this.focusedInstance.isRoot){break;}
					if(this.focusedInstance.isEditable){break;}
					this.selectedInstances.forEach(function(instance){
						instance.toggleButton();
					})
					break;

				case "Delete":
					e.preventDefault();
					if(this.focusedInstance.isEditable){break;}
				
					//can't delete first instance by design 
					if(this.focusedInstance.noteid == 0){break}

					var focusCandidate = this.focusedInstance.getPreviousVisibleInstance();
					while (focusCandidate in this.selectedInstances){
						focusCandidate = focusCandidate.getPreviousVisibleInstance();
					}


					this.selectedInstances.forEach(function(instance){
						instance.removeFromParent();
					});

					focusCandidate.focus();
					break;

				case "c":
				case "x":
				case "b":
					if (!this.focusedInstance.isEditable && e.ctrlKey){
						if (e.key == "c") {this.copy_state = copy_options.COPY;  }
						if (e.key == "x") {this.copy_state = copy_options.CUT; }
						if (e.key == "b") {this.copy_state = copy_options.DEEPCOPY; }
		
						this.addSelectedInstancesToClipboard();
					}
					break;

				case "v":
					if(!this.focusedInstance.isEditable && e.ctrlKey){
						this.selectedInstances.forEach(function(instance){
							this.copiedInstances.forEach(function(instanceToCopy){
								if (this.canCopy(instance,instanceToCopy)){
									this.copyOne(instance,instanceToCopy,this.copy_state);
								}
							}.bind(this))
						}.bind(this))
					}
					break;


				case "0":
				case "1":
				case "2":
				case "3":
				case "4":
				case "5":
				case "6":
				case "7":
				case "8":
				case "9":
				
					if (e.ctrlKey) {
						e.preventDefault();

						if (!e.altKey){
							this.focusedInstance.collapse_N_levels(Number.parseInt(e.key))
						}

						else {
							this.focusedInstance.expand_N_levels(Number.parseInt(e.key));	
						}
					}

					break;


				case "r":
					if (!e.ctrlKey){break;}
					e.preventDefault();

					var result = prompt("Enter a new title: ", document.title);
					document.title = result;
					break;


			}
		});

		//"break" breaks out of the switch, so it won't hit the second "b" handler.
		//For any second keydown listeners, put their code here.
		document.addEventListener("keydown", e => {
			switch(e.key){
				case "b":
				case "u":
				case "i":
					if (this.focusedInstance.isEditable && e.ctrlKey){
						e.preventDefault();
						var fta = this.focusedInstance.textarea;
						
						if (fta.selectionStart == fta.selectionEnd){
							this.focusedInstance.addTag(e.key)
						}

						else{
							var text = this.focusedInstance.markup;
							var start = fta.selectionStart;
							var end = fta.selectionEnd;
							this.focusedInstance.markup = text.slice(0,start) + `<${e.key}>` + text.slice(start,end) + `</${e.key}>` + text.slice(end);
						}

					}
					break;

			}
		});
	},

	mounted(){
	},

};

</script>

<style scoped>
	
	#NoteContainer {
		display: flex;
		flex-direction: column;
		width: 100%;
		height: 100%; 
		background-color: grey;
		border: 1px solid black;
		margin-top: 3px;
		overflow: auto;
		padding: 1px;
	}

	#NoteContainer.focused { 
	    border: 2px solid red;
	}

</style>