import Vue from 'vue'
import Vuex from 'vuex'
import helpers from './helperfns.js'

Vue.use(Vuex)

export class Note {

	constructor(id){
		this.id = id;
		this.markup = "";
		this.children = [];
		this.parents = [];
	}
}

export default new Vuex.Store({
  state: {
		notes : {

			0 : {
					id : 0,
					markup : "0",
					children : [],
					parents : []
				},

			// 1 : {
			// 		id : 1,
			// 		markup : "1",
			// 		children : [],
			// 		parents : [0]
			// },

			// 2: {
			// 	id: 2,
			// 	markup : "2",
			// 	children : [],
			// 	parents : [1]
			// }
		},

		settings: {
			showMarkup: true,
		}, 

		//Must be >= largest numbered conversion
		version: 2,

		//Conversions are named after the version they're bringing you into. 
		//They assume you are at the soonest previous previous that has conversion code after it.
		conversions: {
			2: function(){
				var id;
				for (id in this.notes){
					var note = this.notes[id];
					note.markup = note.markup.replace("<br/>","\r\n");
				}
			}
		}

	},

	getters: {
		nextID: (state) => {
			var notes = state.notes; 
			var keys = Object.keys(notes);
			return keys.length;
		},

		getNoteByID: (state) => (id)=>state.notes[id],
		
	},

	mutations: {

		setMarkup(state, {note, markup}){
			note.markup = markup;
		},


		registerNewNote(state, id){
			var newNote = new Note(id);
			state.notes = {...state.notes, [id]:newNote};

		},

		addChildToParent(state, {child, parent}){
			parent.children.push(child.id);
			child.parents.push(parent.id);
		},

		removeChildFromParent(state,{child, parent}){ 
				parent.children = parent.children.filter(candidate => candidate!=child.id);
		},

		loadState(state,{new_state,fileTitle}){
			//Run conversions on incoming state if neccessary
			while(new_state.version < state.version){

				var conversion = state.conversions[new_state.version+1];
				if (conversion != null){conversion.apply(new_state)}
				new_state.version+=1;
			}

			var new_notes = new_state.notes;
			var new_root = new_notes[0];
			var old_root = state.notes[0];

			//If the file is empty, replace it with the incoming file. Else, add new notes under existing ones.
			if(state.notes[0].children.length == 0){
				this.replaceState(new_state);
			}
			else{
				helpers.shiftDuplicateIDs(new_notes,state.notes);
				this.state.notes = Object.assign({}, this.state.notes, new_notes); //*
				this.commit('addChildToParent',{child:new_root,parent:old_root});
				new_root.markup = fileTitle;

				//* We must make a new object, since if we add properties to an existing object,
				//  Vue's reactivity system will not keep track of them, as this is done on object creation.
			}
	
			
		},
	},	

})

	// var fileName = tFile.name.slice(0, -4) //Remove dot and extension
	// 			this.renameDocument(fileName);
	// 				renameDocument(newtitle){
	// 		//Overwrite title after a delay, since doing it before DOM changes would trigger observer to add '*'
	// 		setTimeout(function(){document.title=newtitle},50);
	// 	},